@import "../../assets/styles/variables";
@import "../../assets/styles/media";

.model {
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative; }

.select {
  padding-bottom: 48px;
  display: flex;
  flex-wrap: wrap;
  gap: 16px; }

.wrapper {
  padding-bottom: 32px;
  display: flex;
  align-content: baseline;
  flex-wrap: wrap;
  flex-grow: 1; }

.scroll {
  min-height: 426px; }

@include mobileDevice {
  .select {
    padding-bottom: 20px; }
  .label {
    font-size: 11px; } }
